/* styles imported from navbar/index.css */
.navbar {
  width: 100%;
  height: 36px;
  /* vertical-align: bottom; */
}

.navbar div {
  /* display: inline; */
  padding-left: 5px;
  padding-right: 5px;
}

.navbar-items {
  margin: 0;
  padding: 0;
  display: inline-block;
  /* list-style-type: none; */
}

.navbar-items div {
  display: inline;
  width: fit-content;
}
/* 
