.navbar-items.center {
  margin-left: 100px;
  margin-right: 100px;
}

.navbar-items.rhs {
  float: right;
}

.navbar-btn {
  /* display: inline; */
  padding-left: 5px;
  padding-right: 5px;
  padding-top: 2px;
  padding-bottom: 2px;
  border-radius: 0;
}

